.App {
  text-align: center;
}

.black-nav {
  display: flex; /* 수평 정렬 */
  background: #323232;
  align-items: center; /* 세로 정렬 */
  justify-content: space-between; /* 가로 정렬 (동일 간격) */
  width: 100%;
  color: #ffffff;
  padding-left: 20px;
}

div {
  box-sizing: border-box;
}
.list {
  padding-left: 20px;
  text-align: left;
  border-bottom: 1px solid #323232;
}
.list p {
  font-size: 12px;
}

.button-container {
  margin: 10px;
  display: flex; /* 수평 정렬 */
  /* justify-content: flex-end; */
  /* 가로 정렬 (우측 정렬) */
  gap: 5px;
}
.button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: #f5f5f5;
  color: #333;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
.button:hover {
  background-color: #e5e5e5;
}

.modal {
  margin-top: 10px;
  padding: 5px 20px 25px;
  background: #eee;
  text-align: left; /* 텍스트 왼쪽 정렬 */
}
